import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { dormitoryRooms } from 'dump/data';

const columns = [
  {
    title: 'Tòa KTX',
    dataIndex: 'dormitoryBuildingCode',
    key: 'dormitoryBuildingCode',
  },
  {
    title: 'Tầng',
    dataIndex: 'floorNumber',
    key: 'floorNumber',
  },
  {
    title: 'Số phòng',
    dataIndex: 'roomNumber',
    key: 'roomNumber',
  },
  {
    title: 'Số phòng',
    dataIndex: 'roomNumber',
    key: 'roomNumber',
  },
  {
    title: 'Sức chứa',
    dataIndex: 'capacity',
    key: 'capacity',
  },
  {
    title: 'Thiết bị',
    dataIndex: 'device',
    key: 'device',
  },
  {
    title: 'Số tiền/người',
    dataIndex: 'price',
    key: 'price',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      dormitoryRooms.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#dormitory-rooms/form/edit/${record.id}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class ListDormitoryRoomPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Phòng KTX">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#dormitory-rooms/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={dormitoryRooms} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListDormitoryRoomPage />
  </Page>
);



